	module.exports = {"data":{"markdownRemark":{"html":"<p>初めて絵チャットで描いたイラスト。 妖夢は初めて妖々夢やったときから、こいつカブ乗りだって直感したｗ 乗せてみたら、案の定見事に一体化しちゃってるし。ただ、左マフラーにしてしまったのは失敗。妖夢は基本的に改造しないでノーマルのままテクで攻めるタイプだと思ってるｗ  それにしても、カブって不思議だ。多少下手に描いてもカブと認識できてしまうのだから。他のバイクでは少しでも手を抜くとなんのバイクだか分からなくなるからなおさら不思議だ。しかも日本で最も売れてるバイクって言う位だから、日本人はカブに取り憑かれているのだろうか。</p>","frontmatter":{"cover":null,"date":"2004-10-01T00:00:00+09:00","strDate":"2004/9/30","title":"リトルカブ&妖夢"}}},"pathContext":{"path":"/ja/blog/2004/10/01-リトルカブ妖夢/","langKey":"ja"}}

